        var el2 = dom.createTextNode("\n  ");
        dom.appendChild(el1, el2);
        var el2 = dom.createElement("p");
        var el3 = dom.createTextNode("“Baby sign” refers to babies using simplified hand motions based on American Sign Language to communicate their wants and needs.  It is a meaningful way for them to express themselves before they start talking.  Baby sign is popular among families, as it decreases crying and fussing in babies who are attempting to communicate.  Research shows that using sign language with babies who have normal hearing may develop advanced language and cognitive skills.  It also appears to promote increased self-confidence, decreased frustration and aggression, and positive socio-emotional development.");
