        var el3 = dom.createTextNode("​How Can I Make Transitions Easier?");
        dom.appendChild(el2, el3);
        dom.appendChild(el1, el2);
        var el2 = dom.createTextNode("\n  ");
        dom.appendChild(el1, el2);
        var el2 = dom.createElement("p");
        var el3 = dom.createTextNode("Older kids and adults are able to switch between activities and topics within milliseconds, but transitions can be difficult for many younger kids.  We can make the transition easier by externalizing it and reducing the amount of language used.  Two techniques involve a backwards countdown or a visual support.  A backwards countdown is completed by counting down from a given number, or using a timer on your phone.  A visual support is a picture, or series of pictures, which show the desired activity or sequence of activities.");
