        var el4 = dom.createElement("p");
        var el5 = dom.createTextNode("\n      Tracy has been working with our son since February, and she is great.  When we first called Tracy to discuss our son's speech delay, she was very thoughtful in answering all of our questions.  We've seen so much improvement since he started working with Tracy, and he has much more confidence using his words, which has helped in other areas, including separating and interacting with others.  She always follows up after each session with session notes and recommendations for things we should work on during the week.  Tracy is a caring professional, who clearly loves working with her students, and we would definitely recommend her to anyone looking for a speech therapist for their child.\n      ");
