        var el4 = dom.createElement("p");
        var el5 = dom.createTextNode("\n      Tracy has been working with our six year old for a few months and we have been extremely impressed with her.  After receiving therapy through our local school district for two years, we were not seeing the progress that we had hoped for.  She made the transition into private care so easy, we were upset that we hadn't started with her sooner.  Our son looks forward to every session that they have together and we are already seeing changes in his language.  We could not be more pleased!\n      ");
