        dom.appendChild(el3, el4);
        var el4 = dom.createElement("p");
        var el5 = dom.createTextNode("I don't write many reviews, but I had to write about Tracy Trumbell.  We just loved her.  My son had just been released from EI in April, and we really felt that he needed to continue speech services over the summer before entering the school district's phonological program.  I couldn't have found a better speech therapist for my son!  What was meant to just be a bridge of sorts turned into us staying with her until he \"graduated\" from speech services. You can tell Tracy loves what she does and she does it well.  She is resourceful, has great learning activities, and provides \"homework\" for you to work on in between sessions.  My son really looked forward to seeing her and had no idea it was for speech. Her enthusiasm is genuine and I really felt that that played a big part in his progress.");
